    p.mt-5(data-aos="fade-down") El primer ejemplo es con Adobe Illustrator, una vez abierta la aplicacin, se debe ir al men #[b Archivo/Nuevo] y aparece una ventana de configuracin donde se pide las medidas del documento el ancho y alto del mismo. En Corel Draw se hace la misma operacin men #[b Archivo/Nuevo], pero este crea un documento por defecto en A4 o carta que se puede configurar en la barra superior de propiedades en otros tamaos. Si el programa que se va a utilizar es Inkscape, automticamente se crea un documento que tambin es configurable desde el men #[b Archivo/Propiedades del documento]. El atajo o teclado rpido para los 3 programas para crear un documento nuevo es #[b CRTL+N].           
    
